h1, h2 {
  margin: 0;
  padding: 0;
}

html {
  font-family: 'Montserrat', sans-serif;
  /*color: #3700B3;*/
  color: #000;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

a {
  text-align: center;
  margin-top: 10px;
  line-height: 25px;
  text-decoration: none;
  color: #ffffff;
  font-weight: bold;
  font-size: 13px;
  cursor: pointer;
  height: 25px;
  width: 100%;
  background: #3700B3;
  border-radius: 2px;
}

a:visited {
  color: #ffffff;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a:hover {
  color: #3700B3;
  background: #ffffff;
  border: 1px solid #3700B3;
}

.main__list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.main__list > * {
  margin: auto;
}

.item__title {
  margin-bottom: 5px;
}

.list__item--article {
  position: relative;
  padding: 10px;
  font-size: 10px;
  border: 1px solid lightgrey;
  border-radius: 2px;
  height: 300px;
  width: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  z-index: 1000;
}

.item--wrap {
  
  padding: 5px;
  background: #ffffff;
  border-radius: 1px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.list__item {
  margin-bottom: 10px;
}

.list__item--article > * {
  z-index: 1;
}

.item__img {
  top: 0;
  left: 50%;
  -webkit-transform: translate(-50%);
      -ms-transform: translate(-50%);
          transform: translate(-50%);
  height: auto;
  position: absolute;
  max-width: 100%;
  z-index: 1;
}

.movie-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.movie {
  width: 70%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: column;
      flex-direction: column;
}

.movie__link {
  width: 50%;
}
.main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.main__search {
  width: 40%;
  height: 40px;
  margin-bottom: 30px;
  -ms-flex-item-align: center;
      align-self: center;
  text-align: center;
}

.main__search:hover {
  background-color: #3700B3;
  color: #ffffff;
  font-size: 20px;
}


